body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --off-white: #f1e8e8;
}
 .App {
   background-color: rgb(190, 248, 242);
 }
.nav {
  background-color: #f1e8e8;
  background-color: var(--off-white);
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-logo {
  height: 60px;
  width: 160px;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  max-width: 900px;
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.nav-item {
  display: inline;
  font-size: 18px;
  font-family: cursive;
  margin-left: 2rem;
  color: rgb(207, 86, 86);
}

.nav-item:hover {
  color: black;
  cursor: pointer;
}

.section{
  border-radius: 20px;
  background:white;
}
.section-dark {
  border-radius: 20px;
  background: #333;
  color: white;
}

.section-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
}

.section-content h1 {
  font-family: sans-serif;
}

.altbody {
  padding-top: 10px;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 10px;
}

.aboutme_main {
    text-align: center;
}

.aboutme_main_img {
    width: 400px;
    height: 400px;
    border-radius: 400px;
}
.cp_pro {
    font-size: 35px;
    font-style: italic;
}

.logo_img{
    width: 170px;
    height: 60px;
}

.cp_pro a {
    text-decoration: none;
    color:rgb(118, 118, 248);
}

.cp_pro a:hover {
    color:rgb(231, 75, 75);
}
.container {
    border : 2px solid rgb(192, 126, 126) ;
    border-radius: 8px;
    padding: 8px;
    display: flex;
    align-items: center;
    justify-content: center
  }
  
  img {
    max-width: 100%
  }
  
  .image {
    flex-basis: 40%
  }
  
  .text {
    font-size: 20px;
    padding-left: 20px;
  }

  .text a {
    background-color: rgb(66, 111, 196);
    color: white;
    padding: 0.05em .5em;
    text-decoration: none;
    text-transform: uppercase;
  }

  
.logoset {
    text-align: center;
    font-size: x-large;
}

.logoset img{
    width: 50px;
    height:50px;
}

.logoset a {
    text-decoration: none;
    color:rgb(118, 118, 248);
}

.logoset a:hover {
    color:rgb(231, 75, 75);
}

